/* Light theme -------------------------------------------------------------- */
body {
  /* stylelint-disable-line no-duplicate-selectors */
  color: #292929;
  background-color: #fff;
}

body a {
  color: #256bd1;
}

body a:hover, body a:focus {
  color: #3585f9;
}

body a:active {
  color: #256bd1;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme {
  color: #fff;
  background-color: #2a2d34;
}

.dark-theme a {
  color: #499cfe;
}

.dark-theme a:hover, .dark-theme a:focus {
