/* -----------------------------------------------------------------------------
  Component: Brand and logo

  Style for the element containing the logo in the header.
----------------------------------------------------------------------------- */
.navbar-brand {
  position: relative;
  display: flex;
  align-content: flex-end;
}

.navbar-brand .logo-link {
  position: absolute;
  height: 28px;
}

.navbar-brand .logo-link.docs {
  right: 0;
  padding-right: 32%;
}

.navbar-brand .logo-link.web {
  left: 0;
  padding-left: 65%;
}

.navbar-brand img {
  width: auto;
  height: 28px;
}

@media (max-width: 575px) {
  .navbar-brand img {
    height: 24px;
  }
}

/* Light theme -------------------------------------------------------------- */
body .logo-link.docs {
  color: #292929;
}

body .logo-light {
  display: inline-block;
}

body .logo-dark {
  display: none;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .logo-link.docs {
  color: #fff;
}

.dark-theme .logo-light {
  display: none;
}

.dark-theme .logo-dark {
  display: inline-block;
}

/* -----------------------------------------------------------------------------
  Component: Mode switch

  Style for the component that switches the theme style to light or dark mmode.
----------------------------------------------------------------------------- */
#light-dark-switch {
  display: flex;
  flex-direction: row;
}

@media (min-width: 1200px) {
  #light-dark-switch {
    padding-bottom: 8px;
  }
}

#light-dark-switch > span:first-child {
  margin-left: 4px;
}

@media (max-width: 575px) {
  #light-dark-switch .text {
    display: none;
  }
}

#light-dark-switch .switch-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  margin-left: 5px;
  background-position: center;
  background-size: contain;
}

#mode-switch {
  position: relative;
  top: 1px;
  box-sizing: content-box;
  display: flex;
  flex-direction: row;
  width: 26px;
  height: 16px;
  padding: 0;
  margin: 0 5px;
  border: 1px solid;
  border-radius: 25px;
}

#mode-switch.dark {
  justify-content: flex-end;
}

#mode-switch.dark span {
  top: 0;
  left: calc(100% - 16px);
}

#mode-switch span {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  content: "";
  background-color: #fff;
  border-radius: 50%;
  transition: left ease-in .075s;
}

/* Light theme -------------------------------------------------------------- */
body #light-dark-switch {
  color: #292929;
}

body #light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun-lt");
}

body #light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon-lt");
}

body #mode-switch {
  background-color: #256bd1;
  border-color: #256bd1;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme #light-dark-switch {
  color: #fff;
}

.dark-theme #light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun-dt");
}

.dark-theme #light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon-dt");
}

.dark-theme #mode-switch {
  background-color: #499cfe;
  border-color: #499cfe;
}

/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  overflow: hidden;
  border: 2px solid;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 40px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 38px;
  height: 38px;
  padding: 5px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 38px 38px;
  border: none;
  border-radius: calc(10px - 2px);
}

/* Light theme -------------------------------------------------------------- */
body .search-box {
  background-color: #fff;
  border-color: #c1ccd9;
}

body .search-box:focus-within {
  border-color: #3585f9;
}

body .search-box .form-control {
  color: #292929;
  background-color: #fff;
}

body .search-box .btn-search {
  background: url("../../images/icons/svg-icons.svg#search-lt");
}

body .search-box ::placeholder {
  color: #a6b5c8;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .search-box {
  background-color: #2a2d34;
  border-color: #fff;
}

.dark-theme .search-box:focus-within {
  border-color: #6ca2f0;
}

.dark-theme .search-box .form-control {
  color: #fff;
  background-color: #2a2d34;
}

.dark-theme .search-box .btn-search {
  background: url("../../images/icons/svg-icons.svg#search-dt");
}

.dark-theme .search-box ::placeholder {
  color: #a6b5c8;
}

