main .toctree-wrapper > ul .toctree-l1 {
  position: relative;
  width: calc(33.333% - 70px);
  min-height: 133px;
  margin-bottom: 60px;
  margin-left: 70px;
}

@media (max-width: 991px) {
  main .toctree-wrapper > ul .toctree-l1 {
    margin-left: 50px;
  }
}

@media (max-width: 767px) {
  main .toctree-wrapper > ul .toctree-l1 {
    width: calc(100% - 70px);
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  main .toctree-wrapper > ul .toctree-l1 {
    width: calc(50% - 70px);
  }
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle {
  left: -3px;
  display: inline-block;
  padding: 10px 0;
  font-size: 18px;
  font-weight: 600;
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle:focus-visible::before {
  border-bottom: 1px solid;
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle::before {
  display: inline-block;
  width: 48px;
  height: 22px;
  content: "More";
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle::after {
  display: inline-block;
  width: 24px;
  height: 12px;
  margin-left: 3px;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 15px;
  transform: rotateX(180deg);
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed ul {
  position: relative;
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed .toggle::after {
  transform: rotateX(0);
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed .toctree-l2:nth-of-type(n + 4) {
  display: none;
}

main .toctree-wrapper > ul .toctree-l1 a,
main .toctree-wrapper > ul .toctree-l1 .section-title {
  position: relative;
  left: -3px;
  display: inline-block;
  padding-left: 15px;
  font-weight: 500;
  line-height: 32px;
  border-left: 3px solid;
}

main .toctree-wrapper > ul .toctree-l1 a:hover,
main .toctree-wrapper > ul .toctree-l1 .section-title:hover {
  text-decoration: none;
}

main .toctree-wrapper > ul .toctree-l1 a:focus-visible,
main .toctree-wrapper > ul .toctree-l1 .section-title:focus-visible {
  outline: none;
  text-decoration: underline;
}

main .toctree-wrapper > ul .toctree-l1 > a,
main .toctree-wrapper > ul .toctree-l1 > .section-title {
  padding-bottom: 15px;
  font-size: 22px;
  font-weight: 600;
  line-height: 22px;
}

@media (max-width: 1199px) {
  main .toctree-wrapper > ul .toctree-l1 > a,
  main .toctree-wrapper > ul .toctree-l1 > .section-title {
    font-size: 20px;
  }
}

main .toctree-wrapper > ul .toctree-l2,
main .toctree-wrapper > ul .toctree-l3 {
  position: relative;
}

main .toctree-wrapper:last-of-type .toctree-l1:last-of-type .toctree-l2 {
  position: relative;
}

main .toctree-wrapper:last-of-type .toctree-l1:last-of-type .toctree-l2 > a {
  display: inline-block;
  position: absolute;
  height: 22px;
  bottom: -1em;
  border-left: none;
  padding: 10px 0;
  left: -3px;
  font-size: 18px;
  font-weight: 600;
}

main .toctree-wrapper:last-of-type .toctree-l1:last-of-type .toctree-l2 > a i {
  margin-left: 12px;
  font-size: 1.1rem;
  position: relative;
  bottom: -1px;
}

main .toctree-wrapper:last-of-type .toctree-l1:last-of-type .toctree-l2 > ul {
  display: block;
}

main .toctree-wrapper:last-of-type .toctree-l1:last-of-type .toctree-l2 > ul .toctree-l3:nth-of-type(n+4) {
  display: none;
}

main .toctree-wrapper:last-of-type .toctree-l1:last-of-type .toctree-l2:nth-of-type(n+2) {
  display: none;
}

main .toctree-wrapper:last-of-type .toctree-l1 .toctree-l2 > ul {
  display: none;
}

